/* For 640 or Up to 767 */

@media only screen and (max-width:767px) {
    .menu_bar {
        top: 26px;
        left: 45px;
    }
    .header_menu {
        padding-left: 10px;
        margin-left: 19px;
    }
    .online-class {
        -webkit-flex: 0 0 50%;
                flex: 0 0 50%;
    }
    .container .row .my-5 {
        margin: 1rem 0!important;
    }
    .header {
        margin-bottom: 20px;
    }
    .card-dashboard {
        margin-top: 20px;
    }
    /* Teacher Page */
    .profile_submenu {
        left: -50px;
        right: -9px;
        top: 70px;
    }
    .assignment_table {
        background: white;
    }
    /* .card.mt-4.card_div {
        margin-top: 0 !important;
    } */
    .card-data {
        font-size: 18px;
    }
    .card-body.filters {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

}

/* For 540 or Up to 639 */

@media only screen and (max-width:639px) {
    .header_search .input-search {
        width: 130px;
    }
    .user_info {
        margin-left: 10px;
    }
    .online-class {
        -webkit-flex: 0 0 100%;
                flex: 0 0 100%;
    }
    /* Teacher Page */
    .profile_submenu {
        left: -55px;
    }
    /* Assignment */
    .assignment_table {
        background: white;
    }
    .iframe_size {
        height: 450px;
    }
    
}

/* For 480 or Up to 539 */

@media only screen and (max-width:539px) {
    .person_name, .person_role {
        display: none;
    }
    .menu_bar {
        top: 15px;
        left: 40px;
    }
    .user_info {
        width: 22px;
    }
    .profile_submenu {
        left: -149px;
    }
    .header_menu {
        width: 50px;
    }
    /* Attendance Page */
    .profile_submenu {
        left: -155px;
        right: -44px;
        top: 55px;
    }
    .attendance_filter {
        padding-right: 0;
    }
}

/* For 379 or Up to 479 */

@media only screen and (max-width:479px) {
    .login_card .card-body {
        padding: 30px;
    }
    .remember_me_forgot_password {
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-flex-direction: column;
                flex-direction: column;
        text-align: center;
    }
    .page_banner {
        display: none;
    }
    .logo_img {
        height: 70px;
        width: 70px;
    }
    .school_name h1 {
        font-size: 18px;
    }
    .school_name p {
        margin: 0;
    }
    .login_page .left_area {
        padding: 25px 0;
    }
    .header_search .input-search {
        width: 100px;
    }
    .profile_submenu {
        left: -180px;
        right: -20px;
    }
    .attendance_table .table-body {
        background: white;
    }
    .card-body.d-flex.justify-content-between.align-items-center.live_class_content {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}

/* For 360 or Up to 379 */

@media only screen and (max-width:379px) {
    .header_menu {
        margin-left: 5px;
    }
    .fixed-top {
        width: 200px;
    }
    .profile_submenu {
        left: -180px;
        right: -14px;
    }
    .col-12.d-flex.justify-content-end.add_btn {
        -webkit-justify-content: start !important;
                justify-content: start !important;
    }
    .iframe_size {
        height: 400px;
    }
    .card-detail {
        padding-right: 18px;
    }
}

/* For 320 or Up to 359 */

@media only screen and (max-width:359px) {
    .login_page .right_area {
        padding: 20px;
    }
    .header_search .input-search {
        width: 90px;
    }
    .notification img {
        width: 35px;
    }
    .header_menu {
        margin-left: 5px;
        padding: 5px;
    }
    .header_menu img {
        width: 35px;
    }
    .header_search .input-search, .header_search .btn-search {
        height: 34px;
    }
    .menu_bar {
        top: 13px;
        left: 40px;
    }
    .custom-badge {
        margin-right: 30px;
    }
    button.btn.btn-link.text-dark.btn-style {
        padding: 0;
    }
    .profile_submenu {
        left: -222px;
        right: 0px;
    }
    .custom-badge {
        margin-right: 45px;
    }
    .card-detail {
        padding-right: 10px;
    }
}
